import CollectionPlayer from "./CollectionPlayer";
import globalState from "@/store/globalState";
import util from "@/util";
import Employee from "@/Employee";
import ValueRange from "@/ValueRange";
import {deserialize, identifier, list, object, primitive, raw, serializable} from "serializr";
import EffectType from "@/EffectType";

export default class Research {

  @serializable
  private thumb: string;
  @serializable(identifier())
  public readonly id: string
  @serializable
  public readonly name: string
  @serializable(object(ValueRange))
  public readonly cost: ValueRange
  @serializable
  public readonly scienceCost: number
  @serializable
  public readonly description: string;
  @serializable(list(primitive()))
  public readonly requires: string[] = []
  @serializable(object(EffectType))
  public readonly effectType: EffectType
  @serializable(raw())
  public readonly effects: any = {}

  @serializable(object(ValueRange))
  public completion: ValueRange
  @serializable
  public isBeingResearched: boolean = false

  constructor(info: any = {}) {
    this.id = (info.id) ? info.id : info.title
    this.name = info.title
    this.thumb = info.thumb
    this.description = info.description
    this.requires = info.requires
    this.cost = new ValueRange(info.cost, 0)
    this.scienceCost = info.scienceCost

    // @ts-ignore
    this.effectType = (info.effectType) ? deserialize(EffectType, info.effectType) : new EffectType()

    this.effects = (info.effects) ? info.effects : {}
    this.completion = new ValueRange(0, 0, this.scienceCost)
  }

  load(state: any) {
    this.completion.fraction = state.completion.fraction
    this.isBeingResearched = state.isBeingResearched
  }

  research(dt: number) {
    this.completion.baseValue += dt
  }

  get isCompleted(): boolean {
    return this.completion.fraction >= 1
  }

  get isStarted(): boolean {
    return this.completion.fraction > 0 || this.isBeingResearched
  }

  get image(): string {
    return util.baseUrl() + '/' + this.thumb
  }

  get displayTarget(): string {
    return util.capitalize(this.effectType.target)
  }

  get displayPermanence(): string {
    return util.capitalize(this.effectType.permanence)
  }

  get displayDuration(): string {
    if (this.effectType.timeType == 'instant') {
      return 'Instant'
    } else {
      return `Maximum after ${util.displayTime(this.effectType.duration)}`
    }
  }
}
