import FarmEvent from "@/FarmEvent";
import {list, object, serializable} from "serializr";
import Farm from "@/Farm";
import util from "@/util";
import Employee from "@/Employee";

export default class EventManager {
  @serializable(list(object(FarmEvent)))
  public events: any[] = []
  @serializable
  public lastEventTime: number = 0
  @serializable
  public nextEventTime: number = 5 + Math.random() * 120
  @serializable
  public lastDay: number = 0

  private get allRandomEvents(): any[] {
    return [
      { likelihood: 10,
        event: () => new FarmEvent(
          "You found ${money}!",
          (args: any) => {
            args.farm.state.money += args.money
          }, (args: any) => {
            let admins = args.farm.getEmployeesAssignedTo('office')
            return {
              money: Math.round(100 * Employee.totalIQ(admins).fraction * admins.length * Math.random())
            }
          }
        )
      },

    ]
  }

  constructor() {
  }

  update(dt: number, farm: Farm) {
    this.lastEventTime += dt

    if (this.lastEventTime > this.nextEventTime) {
      this.addEvent(this.randomEvent, farm)

      this.lastEventTime = 0
      this.nextEventTime = 5 + Math.random() * 120
    }

    let day = Math.floor(util.timeToDays(farm.state.time))
    if (day != this.lastDay) {
      this.lastDay = day

      this.addEvent(FarmEvent.milkPriceChangeEvent(), farm)
    }
  }

  load(state: any) {
    this.lastEventTime = state.lastEventTime
    this.nextEventTime = state.nextEventTime
  }

  private addEvent(event: FarmEvent, farm: Farm) {
    event.takeAction(farm)

    this.events.unshift(event)
    if (this.events.length >= 10) {
      this.events = this.events.slice(0, this.events.length - 1)
    }
  }

  private get randomEvent(): FarmEvent {
    let choice = this.totalEventLikelihood * Math.random()
    let sum = 0
    for (let eventCreator of this.allRandomEvents) {
      sum += eventCreator.likelihood
      if (sum >= choice) {
        return eventCreator.event()
      }
    }
    return new FarmEvent("Nothing happened.", 0, () => {})
  }

  private get totalEventLikelihood(): number {
    let sum = 0
    for (let event of this.allRandomEvents) {
      sum += event.likelihood
    }
    return sum
  }

}
