import CollectionPlayer from "@/CollectionPlayer";
import util from "@/util";
import ValueRange, {MapStringAny} from "@/ValueRange";
import { reactive } from "vue";
import Research from "@/Research";
import {
  custom,
  deserialize,
  identifier,
  list,
  map,
  object,
  primitive,
  raw,
  reference,
  serializable,
  SKIP
} from "serializr";
import ValueMod from "@/ValueMod";
import EffectManager from "@/EffectManager";
import Farm from "@/Farm";
import Laboratory from "@/Laboratory";
import globalState from "@/store/globalState";

export default class Employee {
  @serializable(custom(v => {}, (json, context) => new CollectionPlayer("liliana", context.json.framesInfo)))
  private player: CollectionPlayer = new CollectionPlayer()
  @serializable(raw())
  private readonly framesInfo: any = {}

  @serializable(identifier(primitive))
  public readonly uuid: string = util.uuid()
  @serializable(list(primitive()))
  public names: string[] = []

  // Stats
  @serializable(object(ValueRange))
  public iq: ValueRange = new ValueRange(80 + Math.random() * 40, 20, 180,
    ["Brainfucked", "Fuckdoll", "Brainless", "Airhead", "Ditzy", "Average", "Bright", "Astute", "Smart", "Brilliant", "Genius"]
  )

  @serializable(object(ValueRange))
  public milk: ValueRange = new ValueRange(0, 0, 0, [], true)
  @serializable(object(ValueRange))
  public milkDelta: ValueRange = new ValueRange(Math.max(0.01 * Math.random() - 0.05, 0), 0, 1000)

  @serializable(object(ValueRange))
  public mood: ValueRange = new ValueRange(5, 0, 10, ["Depressed", "Sad", "Moody", "Content", "Happy", "Joyful", "Ecstatic"])
  @serializable(object(ValueRange))
  public moodDelta: ValueRange = new ValueRange(0, -1, 1)

  // Other properties
  @serializable
  public assignment: string = 'farm' // office, farm, lab
  @serializable
  public isBeingMilked: boolean = false
  @serializable(list(object(Research)))
  private equipment: Research[] = []
  @serializable(object(ValueRange))
  public salary: ValueRange = new ValueRange(this.targetSalary, 0)
  @serializable
  private _money: number = 0
  @serializable(object(EffectManager))
  private effectManager: EffectManager = new EffectManager()

  get effectToModsMap() {
    return {
      "iq": this.iq.valueMods,
      "production": this.milkDelta.valueMods,
      "mood": this.mood.valueMods
    }
  }

  constructor() {
    let firstnames = require("@/assets/names.json").firstnames
    this.names = util.sample(firstnames)

    let framesInfo = JSON.parse(globalState().liliana.value)
    if (framesInfo != undefined) {
      this.framesInfo = framesInfo
      this.player = new CollectionPlayer("liliana", this.framesInfo)
    }
  }

  load(state: any, lab: Laboratory) {
    this.names = state.names

    this.iq.load(state.iq)
    this.milk.load(state.milk)
    this.milkDelta.load(state.milkDelta)
    this.mood.load(state.mood)
    this.moodDelta.load(state.moodDelta)

    this.assignment = state.assignment
    this.salary.load(state.salary)
    this._money = state._money
    this.isBeingMilked = state.isBeingMilked

    let equipment: Research[] = []
    for (let item of state.equipment) {
      let realItem = lab.researchById(item.id)
      if (realItem != undefined) equipment.push(realItem)
    }
    this.equipment = equipment

    this.effectManager = deserialize(EffectManager, state.effectManager)
    this.effectManager.refreshEffects(lab, this.effectToModsMap)
  }

  update(dt: number) {
    if (this.assignment === 'farm') {
      this.milkDelta.valueMods.assignmentAdd = new ValueMod(0.01, 'add')
      this.milkDelta.valueMods.assignmentMul = new ValueMod(1, 'mul')
    } else {
      this.milkDelta.valueMods.assignmentAdd = new ValueMod(0, 'add')
      this.milkDelta.valueMods.assignmentMul = new ValueMod(0.5, 'mul')
    }

    this.milk.maxMods["breastSize"] = new ValueMod(50 * Math.pow(this.bimbofactor, 4), 'add')
    this.milk.baseValue += this.milkDelta.value * dt
    this.mood.baseValue += this.moodDelta.value * dt

    this.effectManager.update(dt, this.effectToModsMap)
  }

  equip(research: Research) {
    if (!this.canEquip(research)) return

    this.equipment.push(research)
    this.effectManager.addResearch(research, this.effectToModsMap)
  }

  unequip(research: Research){
    if (this.isEquipped(research)) {
      this.equipment.splice(this.equipment.findIndex(item => item.id === research.id), 1)
      this.effectManager.removeResearch(research, this.effectToModsMap)
    }
  }

  toggleEquip(research: Research) {
    if (this.isEquipped(research)) {
      this.unequip(research)
    } else {
      this.equip(research)
    }
  }

  canEquip(research: Research): boolean {
    return research.effectType.target == "employee" && !this.isEquipped(research);
  }

  isEquipped(research: Research): boolean {
    return this.equipment.find(e => e.id === research.id) != undefined
  }

  get targetSalary(): number {
    return (this.iq.baseValue / this.iq.max) * 1 + this.milkDelta.baseValue * 1
  }

  get bimbofactor(): number {
    return 1 - this.iq.fraction
  }

  get image(): string {
    let index = Math.floor(this.bimbofactor * this.player.activeSequence().getLastIndex())
    this.player.activeSequence().goto(index)
    return this.player.activeSequence().get()
  }

  pay(amount: number) {
    this._money += amount
  }

  get money(): number {
    return this._money;
  }

  get name(): string {
    return util.pickFrom(this.names, this.bimbofactor)
  }

  static totalIQ(employees: Employee[]): ValueRange {
    if (employees.length == 0) return new ValueRange(20, 20, 180)

    let value = 0
    let min = 0
    let max = 0
    for (let employee of employees) {
      value += employee.iq.value
      min += employee.iq.min
      max += employee.iq.max
    }
    return new ValueRange(value, 0, max)
  }
}
