import {reactive, toRaw} from "vue";
import Farm from "@/Farm";

export default class SaveSystem {

  private state = reactive({farm: new Farm()})

  constructor() {
    this.state.farm = new Farm()
  }

  saveGame() {
    this.savetoLocalStorage(this.state.farm.state)
  }

  loadGame(saveName: string) {
    let saves = this.loadAllFromStorage()
    let save = saves[saveName]
    if (save == undefined) {
      console.log("Could not load", saveName)
      return
    }
    this.useSave(save)
  }

  deleteGame() {
    this.deleteFromLocalStorage(this.state.farm.state.saveName)
    let saves = this.loadAllFromStorage()
    if (Object.keys(saves).length == 0) {
      this.newGame()
    } else {
      this.useSave(saves[Object.keys(saves)[0]])
    }
  }

  newGame() {
    this.state.farm = new Farm()
    let saveName = "Default Save"
    let i = 2
    while (saveName in this.allSaves) {
      saveName = "Default Save " + i++
    }
    this.farm.state.saveName = saveName
    this.saveGame()
  }

  get farm() {
    return this.state.farm
  }

  get allSaves(): {[key: string]: any} {
    return this.loadAllFromStorage()
  }

  get gameVersion(): string {
    return new Farm().state.gameVersion
  }

  private useSave(state: any) {
    this.state.farm = new Farm()
    this.state.farm.load(state)
  }

  private deleteFromLocalStorage(saveName: string) {
    let saves = this.loadAllFromStorage()
    delete saves[saveName]
    localStorage.setItem("milkfarm-saves", JSON.stringify(saves))
  }

  private savetoLocalStorage(state: any) {
    let milkfarmState = toRaw(state)
    let saves = this.loadAllFromStorage()
    saves[state.saveName] = milkfarmState
    localStorage.setItem("milkfarm-saves", JSON.stringify(saves))
  }

  private loadAllFromStorage() {
    let savesString = localStorage.getItem("milkfarm-saves")
    let saves: {[key: string]: any} = {}
    if (savesString != null) {
      saves = JSON.parse(savesString)
    }
    //console.log("Reading saves", saves)
    return saves
  }
}
