<template>
  <div class="farm-window employee-card" :style="cssVars">
    <img class="employee-thumb" :src="employee.image"/>
    <div class="info-panel">
      <h2>{{ employee.name }}</h2>
      <div class="stat-bars">
        <p :title="`Too little means ${employee.name} might get sad, too much and you may soon be out of cash yourself!`">Salary</p>
        <div class="option-buttons">
          <p style="margin-right: 1ch;">$ {{ (employee.salary.value * 60).toFixed(0) }} / day </p>
          <TextButton class="farm-button" @click="employee.salary.baseValue -= 0.1"> <p>-</p> </TextButton>
          <TextButton class="farm-button" @click="employee.salary.baseValue += 0.1"> <p>+</p> </TextButton>
        </div>

        <p :title="`Tell ${employee.name} which tasks she should be doing.`">Assignment</p>
        <div class="option-buttons">
          <TextButton @click="farm.assignWork(employee, 'office')"
                      class="farm-button"
                      :class="{'selected-ass': employee.assignment === 'office'}"
                      :title="employee.name + ' will do all the tasks that keep the farm running.'">
            <p>Office</p>
          </TextButton>

          <TextButton @click="farm.assignWork(employee, 'farm')"
                      class="farm-button"
                      :class="{'selected-ass': employee.assignment === 'farm'}"
                      :title="employee.name + ' will focus on eating and sleeping well to produce more milk.'">
            <p>Farm</p>
          </TextButton>
          <TextButton @click="farm.assignWork(employee, 'lab')"
                      class="farm-button"
                      :class="{'selected-ass': employee.assignment === 'lab'}"
                      :title="employee.name + ' will use their intelligence to unlock new research quicker.'">
            <p>Lab</p>
          </TextButton>
        </div>

        <p :title="`More smarts means more science, if assigned to the lab.`">Intelligence</p>
        <ValueMeter :container="employee.iq" barColor="#149">
          <p>{{ employee.iq.value.toFixed(0) }} IQ ({{ employee.iq.text }})</p>
        </ValueMeter>

        <p :title="`You want ${employee.name} to be happy, right?`">Happiness</p>
        <ValueMeter :container="employee.mood" barColor="#194">
          <p>{{ employee.mood.text }} ({{ deltaSymbol(employee.moodDelta.value) }})</p>
        </ValueMeter>

        <p title="Nature's white gold. This is what we're here for.">Milk</p>
        <div class="option-buttons" style="grid-template-columns: 2fr 1fr">
          <ValueMeter :container="employee.milk"
                      barColor="#abc"
                      style="border-right: 0;"
                      :title="`Max storage: ${employee.milk.max.toFixed(1)} L`">
            {{ employee.milk.value.toFixed(2) }} L ({{ (employee.milkDelta.value * 60).toFixed(2) }} L/day)
          </ValueMeter>
          <TextButton class="farm-button"
                      @mousedown="farm.startMilking(employee)"
                      @touchstart="farm.startMilking(employee)"
                      @mouseup="farm.stopMilking(employee)"
                      @mouseleave="farm.stopMilking(employee)"
                      @touchend="farm.stopMilking(employee)"
          >
            <p><b>Milk</b> <i>({{ (farm.state.milkingSpeed.value * 60).toFixed(1) }} L/day)</i></p>
          </TextButton>
        </div>
      </div>
      <div class="equipment-list">
        <template v-for="research in farm.state.lab.availableForEmployee(employee)">
          <img class="research-thumb"
               :src="research.image"
               @click="employee.toggleEquip(research)"
               :class="{ equipped: !employee.canEquip(research) }"
               :title="`${research.name} (${research.displayPermanence}. ${research.displayDuration}.)`"
          />
        </template>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Employee from '@/Employee'
import Farm from '@/Farm'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'

@Options({
  components: {
    ValueMeter, TextButton
  },
  props: {
    employee: Employee, farm: Farm
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    deltaSymbol(value: number): string {
      if (value > 0) return 'Increasing'
      else if (value < 0) return 'Decreasing'
      return 'Stable'
    }
  },
  computed: {
    cssVars() {
      return {
        '--background-image': 'url(' + require('@/assets/wolfgang-hasselmann.jpg') + ')'
      }
    }
  }
})
export default class EmployeeCard extends Vue {
  employee!: Employee
  farm!: Farm
}
</script>

